<template>
    <h1>GitHub</h1>
    <input type="text" placeholder="Repository Name:" v-model="repo" required/>
    <br />
    <br />
    <br />
    <br />
    <input type="text" placeholder="Owner Name:" v-model="owner" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getGithub">Confirm</button>
</template>

<script>    
// import axios from "@/api/axios";
// var validator = require("email-validator");
// const qs = require('qs');

export default {
    name: "GithubA",
    data() {
        return {
            repo: null,
            owner : null
        }
    },
    methods: {
        getGithub() {
            let A = {
                "repo": this.repo,
                "owner": this.owner
            }
            let Action = {
                "content": JSON.stringify(A),
                "id": "63615dd6b44ce8272da128b5"
            }
            let nameAct = {
                "name": "Github",
                "description": "Get the last issue of a repository"
            }
            localStorage.action = JSON.stringify(Action);
            localStorage.nameact = JSON.stringify(nameAct);
            var myHeaders = new Headers();
            myHeaders.append("token", localStorage.token);
            var requestOptions = {
              method: 'GET',
              headers: myHeaders,
              redirect: 'follow'
            };
            fetch(localStorage.server + "/user/github/verify", requestOptions)
                .then(res => {
                    res.json().then((ans) => {
                        if (ans["status"] == "ok") {
                          this.$router.push({ name: 'Recap' });
                        }
                        else {
                          localStorage.path = 'Recap';
                        
                          var requestOptions = {
                            method: 'GET',
                            headers: myHeaders,
                            redirect: 'follow'
                          };
                      
                          fetch(localStorage.server + "/user/github/getLink", requestOptions)
                            .then(rep => {
                                if (rep.status === 200) {
                                    rep.text().then((ans) => {
                                        window.location.href = ans;
                                    })
                                }
                            })
                            .catch(error => console.log('error', error));
                        }
                    })
                })
              .catch(error => console.log('error', error));
        },
    }
}
</script>

